<template>
  <el-dialog 
    v-model="visible" 
    :title="form.id === 0 ? '新增商户' : '编辑商户'" 
    width="700px"
    :close-on-click-modal="false"
  >
    <el-form :model="form" label-width="140px" :rules="rules" ref="formRef">
      <!-- 代理商选择（仅管理员） -->
      <el-row :gutter="20" v-if="!isAgent">
        <el-col :span="24">
          <el-form-item label="代理商" prop="agent_id">
            <el-select 
              v-model="form.agent_id" 
              placeholder="请选择代理商" 
              clearable
              filterable
              :disabled="form.id > 0"
            >
              <el-option
                v-for="agent in agentList"
                :key="agent.id"
                :label="agent.agent_name"
                :value="agent.id"
              />
            </el-select>
            <div class="form-tip" v-if="form.id > 0">代理商创建后不可修改</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="商户名称" prop="merchant_name">
            <el-input 
              v-model="form.merchant_name" 
              placeholder="请输入商户名称" 
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 账号输入 -->
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="账号" prop="username">
            <el-input 
              v-model="form.username" 
              :disabled="form.id > 0"
              placeholder="请输入账号" 
              clearable
            />
            <div class="form-tip" v-if="form.id > 0">账号创建后不可修改</div>
            <div class="form-tip" v-else>账号创建后不可修改，请谨慎填写。默认密码：123456，首次登录需修改</div>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="IP白名单" prop="ip_whitelist">
            <el-input 
              v-model="form.ip_whitelist" 
              type="textarea"
              :rows="3"
              placeholder="多个IP用逗号分隔，例如：192.168.1.1,192.168.1.2" 
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="备注" prop="remark">
            <el-input 
              v-model="form.remark" 
              type="textarea"
              :rows="3"
              placeholder="请输入备注信息" 
              clearable
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item label="状态" prop="status">
            <el-switch 
              v-model="form.status" 
              :active-value="1" 
              :inactive-value="0"
              active-text="启用"
              inactive-text="禁用"
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-alert
        v-if="form.id === 0"
        title="商户创建成功后将自动生成API密钥，请妥善保管"
        type="warning"
        :closable="false"
        style="margin-bottom: 20px"
      />
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button @click="visible = false">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { ref, computed } from 'vue';
import { ElMessage } from 'element-plus';
import request from '@/utils/request';

// 定义emit
const emit = defineEmits(['refresh']);

// 表单ref
const formRef = ref();

// 控制dialog显示
const visible = ref(false);

// 表单数据
const form = ref({
  id: 0,
  agent_id: '',
  merchant_name: '',
  username: '',
  ip_whitelist: '',
  status: 1,
  remark: ''
});

// 用户信息
const userInfo = ref<any>({});
const isAgent = computed(() => userInfo.value?.is_agent === true);

// 代理商列表
const agentList = ref([]);

// 验证规则（动态）
const rules = computed(() => {
  const baseRules: any = {
    merchant_name: [
      { required: true, message: '请输入商户名称', trigger: 'blur' }
    ],
    username: [
      { required: true, message: '请输入账号', trigger: 'blur' },
      { min: 3, max: 50, message: '账号长度在 3 到 50 个字符', trigger: 'blur' },
      { pattern: /^[a-zA-Z0-9_]+$/, message: '账号只能包含字母、数字和下划线', trigger: 'blur' }
    ]
  };
  
  // 只有管理员需要验证agent_id
  if (!isAgent.value) {
    baseRules.agent_id = [
      { required: true, message: '请选择代理商', trigger: 'change' }
    ];
  }
  
  return baseRules;
});

// 获取用户信息（用于判断是否是代理商）
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取代理商列表
const getAgentList = async () => {
  if (isAgent.value) return;
  
  try {
    const res = await request({
      url: '/api/v1/admin/merchant/agent-list',
      method: 'GET'
    });
    if (res.status) {
      agentList.value = res.data;
    }
  } catch (error) {
    console.error('获取代理商列表失败:', error);
  }
};

// 打开dialog
const open = async (row: any) => {
  // 先获取用户信息
  await getUserInfo();
  
  // 然后获取其他数据
  await getAgentList();
  
  if (row.id) {
    // 编辑模式 - 获取详情
    try {
      const res = await request({
        url: `/api/v1/admin/merchant/detail/${row.id}`,
        method: 'GET'
      });
      if (res.status) {
        form.value = { ...res.data };
        console.log('商户详情数据:', form.value); // 调试信息
      }
    } catch (error) {
      console.error('获取详情失败:', error);
      ElMessage.error('获取详情失败');
      return;
    }
  } else {
    // 新增模式 - 重置表单
    const currentAgentId = isAgent.value ? userInfo.value.agent_id : '';
    
    form.value = {
      id: 0,
      agent_id: currentAgentId,
      merchant_name: '',
      username: '',
      ip_whitelist: '',
      status: 1,
      remark: ''
    };
  }
  
  visible.value = true;
  
  // 清除验证
  setTimeout(() => {
    formRef.value?.clearValidate();
  }, 100);
};

// 提交表单
const handleSubmit = async () => {
  try {
    await formRef.value.validate();
    
    // agent_id 处理：后端会从 userData（中间件注入）中获取
    // 代理商：后端自动从 userData 获取，忽略前端传递的值
    // 管理员：从表单中选择，后端验证
    const submitData = { ...form.value };
    
    const url = submitData.id 
      ? '/api/v1/admin/merchant/edit' 
      : '/api/v1/admin/merchant/add';
    
    const res = await request({
      url,
      method: 'POST',
      data: submitData
    });
    
    if (res.status) {
      ElMessage.success(res.msg || '操作成功');
      visible.value = false;
      emit('refresh');
    } else {
      ElMessage.error(res.msg || '操作失败');
    }
  } catch (error) {
    console.error('提交失败:', error);
    if (error !== false) { // 验证失败时返回false
      ElMessage.error('提交失败');
    }
  }
};

defineExpose({
  open
});
</script>

<style scoped>
.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 4px;
}
</style>

